/* EsithStorm - Styles globaux */
:root {
  --primary: #1a5f4a;
  --primary-light: #2d8f6f;
  --accent: #c9a227;
  --bg: #f5f3ef;
  --card: #ffffff;
  --text: #2c2c2c;
  --text-muted: #6b6b6b;
  --border: #e0ddd8;
  --error: #c0392b;
  --font: 'DM Sans', sans-serif;
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  font-family: var(--font);
  font-size: 15px;
  color: var(--text);
  background: var(--bg);
}

a {
  color: var(--primary);
  text-decoration: none;
  &:hover { text-decoration: underline; }
}

.btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  font-family: inherit;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}
.btn-primary {
  background: var(--primary);
  color: white;
  &:hover { background: var(--primary-light); }
}
.btn-secondary {
  background: var(--border);
  color: var(--text);
  &:hover { background: #d0ccc6; }
}
.btn-danger {
  background: var(--error);
  color: white;
  &:hover { opacity: 0.9; }
}

.form-group {
  margin-bottom: 1rem;
  label {
    display: block;
    margin-bottom: 0.35rem;
    font-weight: 500;
    color: var(--text);
  }
  input, select, textarea {
    width: 100%;
    padding: 0.5rem 0.75rem;
    border: 1px solid var(--border);
    border-radius: 6px;
    font-family: inherit;
    font-size: 0.95rem;
    &:focus {
      outline: none;
      border-color: var(--primary);
      box-shadow: 0 0 0 2px rgba(26, 95, 74, 0.2);
    }
  }
  textarea { min-height: 80px; resize: vertical; }
}

.card {
  background: var(--card);
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  padding: 1.5rem;
  margin-bottom: 1rem;
}

.table-wrap {
  overflow-x: auto;
  table {
    width: 100%;
    border-collapse: collapse;
    th, td {
      padding: 0.75rem 1rem;
      text-align: left;
      border-bottom: 1px solid var(--border);
    }
    th {
      font-weight: 600;
      color: var(--text-muted);
      font-size: 0.85rem;
      text-transform: uppercase;
      letter-spacing: 0.03em;
    }
    tr:hover td { background: rgba(0,0,0,0.02); }
  }
}

.container { max-width: 1200px; margin: 0 auto; padding: 0 1.5rem; }

.error-msg { color: var(--error); font-size: 0.9rem; margin-top: 0.25rem; }
